module.exports = "\n# Delorean\n\n---\n\nDelorean is a proposal of a debugger for JavaScript.\n\n## Features\n\n---\n\n  - You can go back to any point of your program\n\n  - We capture the heap and stack of your program\n\n  - Continuations use case\n\n## Usage\n\n---\n\nWe expose a simple object to work with our debugger called **Delorean**.\n\n```js\n\n/* Set the variables to debug. */\n\nDelorean.watch(observables: array): void;\n\n/* Create a timepoint with the name received by parameter. */\n\nDelorean.insertTimepoint(name: string): void;\n\n```\n\n## Tech\n\n---\n\nDelorean uses a number of open source projects to work properly:\n\n* [Babel](https://babeljs.io) - awesome and configurable transpiler!\n\n* [Node.js](https://nodejs.org/) - evented I/O for the backend\n\n* [mocha](https://mochajs.org/) - simple, flexible JavaScript test framework\n\n* [unwinder](https://github.com/jlongster/unwinder) - an implementation of continuations in JavaScript\n\nAnd of course Delorean itself is open source with a [public repository](https://github.com/fruizrob/delorean) on GitHub.\n"

